import React from 'react';
import styled from 'styled-components';
import { Section, Container } from '@styles/global';

const StyledContainer = styled(Container)`
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 8px;
    min-height: 250px;
    padding: 3rem 1.5rem;
    box-shadow: 0 0 32px 4px rgba(0, 0, 0, 0.5);

    h3 {
        font-size: 1.5rem;
        span {
            background-color: var(--blue);
            color: var(--alt-color);
            text-transform: uppercase;
            padding: 2px 10px;
            font-size: 0.9rem;
        }
        @media (min-width: ${props => props.theme.screen.sm}) {
            span {
                font-size: 1.2rem;
                letter-spacing: 1px;
            }
        }
    }

    ${props =>
        props.color === 'alt' &&
        `
        background-color: var(--blue);    

        h3 { 
            color: var(--alt-color);
            span {
                background: var(--alt-color);
                color: var(--blue);
            }
        }

        p {
            color: var(--alt-color);            
        }
        
    `};

    @media (min-width: ${props => props.theme.screen.sm}) {
        width: 90%;
    }

    p {
        text-align: left;
        margin-bottom: 0;

        @media (min-width: ${props => props.theme.screen.sm}) {
            text-align: center;
        }
    }

    .button {
        margin-top: 1.666rem;
        border-radius: 35px;
    }
`;

const StyledSection = styled(Section)`
    position: relative;
    background: transparent;
    padding: 0 !important;
    margin-top: -1rem;
    margin-bottom: -8rem;

    @media (min-width: ${props => props.theme.screen.sm}) {
        margin-top: -7.4rem;
    }
`;

export default function Box({ color, children }) {
    return (
        <StyledSection>
            <StyledContainer color={color}>{children}</StyledContainer>
        </StyledSection>
    );
}
