import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import { Section, Container } from '@styles/global';

import { ReactComponent as ImgOne } from '@images/art/undraw_expert.svg';
import { ReactComponent as ImgTwo } from '@images/art/undraw_developer.svg';

const Grid = styled(Container)`
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;
    align-items: center;
    justify-items: center;

    ${props =>
        props.inverse &&
        `
        grid-template-columns: 2fr 3fr;        
    `};

    @media (max-width: ${props => props.theme.screen.sm}) {
        grid-template-columns: 1fr;

        ${props =>
            props.inverse &&
            `
            ${Art} {
              order: 2;
            }
        `}
    }

    .button {
        border-radius: 35px;
    }
`;

const Art = styled.figure`
    svg {
        width: 100%;
        height: auto;
    }
`;

const About = () => (
    <>
        <Section
            id="about"
            style={{ padding: `14rem 0 5rem 0` }}
            accent="magnus"
        >
            <Grid>
                <div>
                    <h2>Venture Capital</h2>
                    <p>
                        We bring in leading venture capital funds and
                        high-net-worth individuals to invest along-side us in
                        groundbreaking companies.
                    </p>
                    <Link to="/venture-capital/">
                        <button className="button small">Find out more</button>
                    </Link>
                </div>
                <Art>
                    <ImgOne />
                </Art>
            </Grid>
        </Section>
        <Section accent="matt">
            <Grid inverse>
                <Art>
                    <ImgTwo />
                </Art>
                <div>
                    <h2>Software Development</h2>
                    <p>
                        With expert developers and business-savvy project
                        managers, we help build cutting-edge products that match
                        user expectations.
                    </p>
                    <Link to="/software-development/">
                        <button className="button small">Find out more</button>
                    </Link>
                </div>
            </Grid>
        </Section>
    </>
);

export default About;
